export default {
	data() {
		return {
			showPinMask: false,
			coreId: '',
			money: '',
			address: '',
			charge: '',
			pin: '',
			allmoneyNum: '',
			id: '',
			array: [],
			coinList:[],
			arrayenName:[],
			index:0,
			memo:'',
			password:'',
			autoCode:'',
			PayPassword:'',
			showCodeMask:false
		}

	},
	onLoad(options) {
		if (!uni.getStorageSync("token") && !uni.getStorageSync("SecretKey")) {
			this.$base1._isLogin()
		}
		this.allmoneyNum = options.money
		//我的资产列表
		uni.request({
			url: this.baseUrl + "/coin-list",
			header: {
				Authorization: uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(res)
				if (this.$base1._indexOf(res.data.status)) {
					this.$base1._isLogin()
				} else if (res.data.status == 1) {
					this.coinList = res.data.data
					var self = this
					for(var i = 0; i < self.coinList.length; i++){
						var coinListName = {enName:self.coinList[i].EnName,id:self.coinList[i].Id}
						self.array.push(coinListName)
					}		
					for(var j=0 ; j<self.array.length;j++){
						this.arrayenName.push(this.array[j].enName)
					}
				} else {
					uni.showToast({
						title: res.data.message,
						icon: 'none'
					})
				}
			}
		})

	},
	methods: {
		bindPickerChange(e) {
			this.index = e.target.value
			console.log(this.index)
		},
		allmoney() {
			this.money = this.allmoneyNum
		},
		sureTransfer() {
			this.showPinMask = true
		},
		closePinMask() {
			this.showPinMask = false
		},
		confirm() {
			this.showCodeMask = true
		},
		closeCodeMask(){
			this.showCodeMask = false
		},
		surePay(){
			//点击确定发送资金验证码，验证成功后提现
			uni.request({
				url: this.baseUrl + "/sms-withdraw",
				method:'POST',
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 1) {
						//请求成功之后转账
						uni.request({
							url: this.baseUrl + "/recharge",
							data:{
								Id:this.array[this.index].id,
								Money:this.money,
								Address:this.address,
								Memo:this.memo,
								AuthCode:this.autoCode,
								PayPassword:this.password
							},
							method:'POST',
							header: {
								//除注册登录外其他的请求都携带用户token和秘钥
								Authorization: uni.getStorageSync('token')
							},
							success: (res) => {
								// console.log(res.data)
								if (res.data.status == 1) {
									
								} else {
									//提示余额不足
									uni.showToast({
										title: res.data.message,
										icon: "none"
									})
									uni.showModal({
										title: '转账失败',
										content: "点击确认继续转账",
										success: function(res) {
											if (res.confirm) {
												uni.redirectTo({
													url: "./currency-detail"
												})
											} else if (res.cancel) {
												uni.redirectTo({
													url: "./currency-detail"
												})
											}
										}
									});
								}
							}
						})
					} else {
						//提示余额不足
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
						uni.showModal({
							title: '转账失败',
							content: "点击确认继续转账",
							success: function(res) {
								if (res.confirm) {
									uni.redirectTo({
										url: "./currency-detail"
									})
								} else if (res.cancel) {
									uni.redirectTo({
										url: "./currency-detail"
									})
								}
							}
						});
					}
				}
			})
		},
		jumpToForgetPassword(){
			uni.navigateTo({
				url:"../login/forgetPassword"
			})
		}
		
	}
}
